				margin: 9px 42px 0px 0px;
				width: 1px;
				border-left: 5px solid #25282B;
				height: 160px;
			}

			&-head {
				font-weight: bold;
				font-size: 36px;
			}

			&-sub {
				margin-top: 20px;
				font-size: 17px;
				line-height: 28px;
			}

			&-img {
				width: 40%;

				.main-icon {
					max-width: 538px;
          width: 100%;
          height: 385px;
				}
			}
		}
	}

	.class-intro {
		width: 100%;
		max-width: 1150px;
		margin: auto;
		border-top: 1px solid #C8CCD1;
		display: flex;
		justify-content: space-between;
		position: relative;

		.intro {

			&-text-box {
				width: 50%;
			}

			&-head {
				margin-top: 80px;
				font-weight: bold;
				font-size: 32px;
			}

      &-sub-head {
        margin-top: 24px;
        font-size: 24px;
        line-height: 36px;
        font-weight: bold;
			}

			&-sub {
				margin-top: 20px;
				font-size: 16px;
				line-height: 30px;
				color: #646B71;
			}

			&-card {
				width: 400px;
				margin-top: 80px;
				background-color: white;
			}

			&-card-fix {
				margin: 0px;
				position: fixed;
				right: 4%;
				top: 20%;
				z-index: 1;
			}

			&-card-one {
				padding: 30px 40px;
				border: 2px solid #25282B;
				border-radius: 20px;
				box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.08);
			}

			&-divide {
				width: 80px;
				height: 26px;
				line-height: 26px;
				font-size: 12px;
				color: white;
				text-align: center;
				background: #4570FB;
				border-radius: 5px;
			}

			&-price {
				font-size: 40px;
				font-weight: bold;
				margin-top: 10px;
			}

			&-sub-price {
				font-size: 14px;
				color: #FB4E4E;
				margin-top: 4px;
				line-height: 24px;
			}

			&-des {
				margin-top: 24px;
				display: flex;
				align-items: center;

				svg {
					width: 24px;
					height: 24px;
					margin-right: 12px;
				}
			}

			&-select {
				margin-top: 40px;
				font-size: 16px;
			}

			&-btn {
				background: #25282B;
				border-radius: 32px;
        width: 153px;
				height: 48px;
				line-height: 48px;
				font-size: 16px;
				color: white;
				text-align: center;
				margin-top: 60px;
				border: 0px solid #007BFF;
				cursor: pointer;

				&:hover {
					background: #007BFF;
					border: 0px solid #007BFF;
				}
			}
		}
	}

	.class-get {
		width: 100%;
		max-width: 1150px;
		margin: auto;

		.get {

			&-head {
				font-weight: bold;
				font-size: 32px;
				margin: 80px 0px 20px;
			}

			&-thing {
				display: flex;
				font-size: 16px;
				margin-top: 12px;

				svg {
					width: 24px;
					height: 24px;
					margin-right: 12px;
				}
			}
		}
	}

	.class-calendar {
		width: 100%;
		max-width: 1150px;
		margin: auto;
		margin-top: 80px;

		.calendar {

			&-header {
				font-weight: bold;
				font-size: 32px;
				margin-bottom: 30px;
			}

			&-week-box {
				border-radius: 8px;
			}

			&-week {
				width: 680px;
				padding: 24px;
				border: 1px solid #DBDDE0;

				&:first-child {
					border-radius: 16px 16px 0px 0px;
				}

				&:last-child {
					border-radius: 0px 0px 16px 16px;
				}
			}

			&-tip-box {
				display: flex;
			}

			&-tip-blue {
				padding: 2px 10px;
				color: white;
				font-size: 12px;
				background-color: #4570FB;
				margin-right: 10px;
				border-radius: 5px;
			}

			&-tip-yellow {
				padding: 2px 10px;
				font-size: 12px;
				background-color: #F8CF4D;
				border-radius: 5px;
			}

			&-head {
				font-size: 20px;
				font-weight: bold;
				margin-top: 10px;
			}

			&-sub {
				font-size: 15px;
				margin-top: 10px;
				color: #646B71;
				line-height: 28px;
			}
		}
	}

	.class-teacher {
		max-width: 1150px;
    width: 100%;
    margin: 80px auto;
		padding: 80px 0px 0px;
		border-top: 1px solid #C8CCD1;

		.teacher {

			&-header {
				font-size: 34px;
				font-weight: bold;
			}

			&-box {
				display: flex;
				flex-wrap: wrap;
				margin-top: 44px;
			}

			&-list {
				display: flex;
				align-items: center;
				margin: 0px 42px 44px 0px;
			}

			&-icon {
				width: 123px;
				height: auto;
				margin-right: 32px;
			}

			&-head {
				font-size: 18px;
				font-weight: bold;
			}

			&-text {
				font-size: 14px;
				color: #646B71;
				margin-top: 8px;
				line-height: 22px;
			}
		}
	}

	.class-become {
		margin-top: 46px;
		text-align: center;

		.become {

			&-head {
				display: flex;
				align-items: center;
				justify-content: center;
				font-size: 32px;
				font-weight: bold;
			}

			&-emoji {
				width: 40px;
				margin-left: 6px;
			}

			&-sub {
				font-size: 16px;
				color: #646B71;
				margin-top: 10px;
			}

			&-btn-box {
				display: flex;
				justify-content: center;
				margin-top: 54px;
			}

			&-btn-black {
				padding: 0px 30px;
				height: 52px;
        line-height: 48px;
				color: white;
				background-color: #25282B;
				margin-right: 40px;
				border-radius: 32px;
				border: 2px solid #25282B;
				cursor: pointer;

				&:hover {
					background-color: #4570FB;
					border: 2px solid #4570FB;
				}
			}

			&-btn-blue {
				padding: 0px 30px;
				height: 52px;
				line-height: 52px;
				color: white;
				background-color: #4570FB;
				border-radius: 32px;
				cursor: pointer;

				&:hover {
					opacity: 0.9;
				}
			}
		}
	}

  .class-popup {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    background-color: rgba(255,255,255,0.9);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;

    .popup {

      &-box {
        width: 430px;
        height: auto;
        padding: 22px 22px 50px;
        background-color: white;
        border-radius: 20px;
        border: 3px solid #25282B;
      }

      &-close {
        font-size: 30px;
        text-align: right;
        cursor: pointer;
      }

      &-head {
        font-weight: bold;
        font-size: 32px;
        margin-top: 6px;
        text-align: center;
      }

      &-sub {
        width: 330px;
        font-size: 16px;
        margin: 12px auto 0px;
        text-align: center;
      }

      &-btn {
        width: 330px;
        height: 48px;
        line-height: 48px;
        font-size: 16px;
        background: #25282B;
				color: white;
        text-align: center;
        margin-top: 46px;
        border: 0px;
				cursor: pointer;
        border-radius: 32px;

				&:hover {
					opacity: 0.9;
          background-color: #007BFF;
				}
      }
    }
  }

}

@media( min-width: 1500px ){
	.school-class {

		.class-intro {

			.intro-card-fix {
				margin: 0px;
				position: fixed;
				right: 13%;
				top: 20%;
				z-index: 1;
			}
		}
	}
}

@media( max-width: 1023px ){

.school-class {
	padding: 0px 20px 100px;

	.class-lesson {
		padding: 54px 0px 30px;

		.lesson {

			&-box {
				width: 100%;
			}

			&-line {
				height: 100%;
				width: 10px;
			}

			&-head {
				font-size: 32px;
			}

			&-sub {
				font-size: 20px;
			}

			&-img {
				display: none;

				img {

				}
			}
		}
	}

	.class-intro {
		flex-direction: column-reverse;

		.intro {

			&-text-box {
				width: 100%;
			}

			&-head {
				margin-top: 50px;
				font-size: 32px;
			}

			&-sub {

			}

			&-card {
				margin-top: 30px;
			}

			&-card-fix {
				position: initial;
			}

			&-card-one {
				padding: 24px;
			}

			&-divide {

			}

			&-price {

			}

			&-des {

				svg {

				}
			}

			&-select {
				margin-top: 36px;
			}

			&-btn {

			}
		}
	}

	.class-get {

		.get {

			&-head {
				margin: 50px 0px 20px;
			}

			&-thing {

				svg {

				}
			}
		}
	}

	.class-calendar {
		margin-top: 50px;

		.calendar {

			&-header {

			}

			&-week {
				width: auto;
			}

			&-head {

			}

			&-sub {

			}
		}
	}

		.class-teacher {

		.teacher {

			&-header {

			}

			&-box {

			}

			&-list {
				flex-direction: column;
				margin: 0px 20px 44px 0px;
				text-align: center;
			}

			&-icon {
				margin-bottom: 20px;
			}

			&-head {

			}

			&-text {

			}
		}
	}

	.class-become {

		.become {

			&-head {

			}

			&-emoji {
				display: none;
			}

			&-sub {

			}

			&-btn-box {
				flex-wrap: wrap;
				margin-top: 24px;
			}

			&-btn-black {
				margin: 0px;
			}

			&-btn-blue {
				margin-top: 10px;
			}
		}
	}

}

}

</style>



